'use strict'

console.log('Start template has loaded')

function closeMenu () {
    $('.header__nav-list__link').removeClass('header__nav-list__link_active')
    $('.header__nav-icon').removeClass('header__nav-icon_active')
    $('.header__nav-sub').removeClass('header__nav-sub_active')
}

// Event for click on dropdown
$('.header__nav-list__link_sub').click(function(e) {
    e.preventDefault()
    if(!$(this).hasClass('header__nav-list__link_active')) {
        $(this).addClass('header__nav-list__link_active')
        $('.header__nav-icon').addClass('header__nav-icon_active')
        $('.header__nav-sub').addClass('header__nav-sub_active')
    } else {
        closeMenu()
    }
})

// Close dropdonw when user has been clicked on non dropdown
$(document).click(e => {
    if ($('.header__nav-sub').hasClass('header__nav-sub_active') && !$(e.target).hasClass('header__nav-list__link')) {
        if (e.target.closest('.header__nav-sub') == null || $(e.target).hasClass('header__nav-sub__link')) {
            closeMenu()
            closeBurger()
        }
    }
})


// Tabs
function changeTabs(item = false) {
    $('.people-tabs__item').removeClass('people-tabs__item_active')
    if (item) {
        $('.people-tabs__item[data-tab-content="'+item+'"]').addClass('people-tabs__item_active')
    }
}

function changeTabsHead(item = false) {
    $('.people-tabs__button').removeClass('people-tabs__button_active')
    if (item) {
        $('.people-tabs__button[data-tabs="'+item+'"]').addClass('people-tabs__button_active')
    }
}

function changeTab(item = false) {
    changeTabs(item)
    changeTabsHead(item)
    localStorage.setItem('tab-value', item)
}

$('.people-tabs__button').click(function (e) {
    changeTab(e.target.dataset.tabs)
})


// Init tab
if (localStorage.getItem('tab-value') == null) {
    localStorage.setItem('tab-value', 1)
}

changeTab(localStorage.getItem('tab-value'))

// Questions

function closeQuestions() {
    $('.questions-accordeon__item').removeClass('questions-accordeon__item_active')
    $('.questions-accordeon__icon').removeClass('questions-accordeon__icon_active')
}
function changeOpenQuestion(item = false) {
    if ($(item).parent().hasClass('questions-accordeon__item_active')) {
        closeQuestions()
        return
    }

    closeQuestions()
    if (item) {
        $(item).parent().addClass('questions-accordeon__item_active')
        $(item).children('.questions-accordeon__icon').addClass('questions-accordeon__icon_active')
    }
}

$('.questions-accordeon__button').click(function(e) {
    changeOpenQuestion($(this))
})

// Footer link
function closeFooterMenu() {
    $('.footer__link_sub').removeClass('footer__link_active')
    $('.footer__icon').removeClass('footer__icon_active')
    $('.footer__nav-sub').removeClass('footer__nav-sub_active')
}

function openFooterMenu() {
    $('.footer__link_sub').addClass('footer__link_active')
    $('.footer__icon').addClass('footer__icon_active')
    $('.footer__nav-sub').addClass('footer__nav-sub_active')
}

$('.footer__link_sub').click(e => {
    e.preventDefault()
    if ($('.footer__link_sub').hasClass('footer__link_active')) {
        closeFooterMenu()
    } else {
        openFooterMenu()
    }
})

// Burger
function closeBurger() {
    $('.burger').removeClass('burger_active')
    $('.header__nav').removeClass('header__nav_active')
    $('body').removeClass('lock')
}

function openBurger() {
    $('.burger').addClass('burger_active')
    $('.header__nav').addClass('header__nav_active')
    $('body').addClass('lock')
}

$('.burger').click(e => {
    if ($('.burger').hasClass('burger_active')) {
        closeBurger()
    } else {
        openBurger()
    }
})

